<template>
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it ju-ar">
      <i class="el-icon-arrow-left" @click="$router.push('/build')"></i>
      <div class="box fl-ex al-it ju-fe">
        <i class="el-icon-search"></i>
        <i class="el-icon-more"></i>
      </div>
    </header>
    <!-- 主体 -->
    <div class="main" v-for="(item,index) in list" :key="index">
      <div class="h1">
        <h1>{{item.h1}}</h1>
      </div>
      <div class="p1">
        <p>{{item.p1}}</p>
      </div>
      <div class="img">
        <img :src="item.pic" alt="图片" />
      </div>
      <div class="p1">
        <p>
          {{item.p2}}
          <br />
          {{item.p3}}
          <br />
          <strong>{{item.p4}}</strong>{{item.p5}}
          <br />
          <strong>{{item.p6}}</strong>{{item.p7}}
          <br />
          <strong>{{item.p8}}</strong>{{item.p9}}
          <br />
          <strong>{{item.p10}}</strong>{{item.p11}}
          <br />
          <strong>{{item.p12}}</strong>{{item.p13}}
          <br />
          <strong>{{item.p14}}</strong>{{item.p15}}
          <br />
          <strong>{{item.p16}}</strong>{{item.p17}}
        </p>
      </div>
      <footer class="fl-ex al-it">
        <p style="margin-left:38%;">阅读</p>
        <p style="margin-left:2%;">|</p>
        <i class="el-icon-view"></i>
        <span>{{item.num}}</span>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          h1: "马吉孝同志在省级群团体组织传达学习全国“俩会”精神会议上的讲话",
          p1:
            "今天，我们在这里召开群团组织传达学习全国两会精神会议，主要目的是：以习近平新时代中国特色社会主义思想为指导，全面贯彻落实党的十九大精神，把思想和行动统一到党中央关于加强和改进新形势下党的群团工作一系列重大决策部署上来，把学习宣传和贯彻落实全国两会精神，转化为群团组织的责任担当和实际行动，加强思想政治引领、凝聚广泛共识，发挥桥梁纽带作用，保持定力、真抓实干、敢于斗争、共同奋斗，团结引导广大群众深入实施“五四战略”、奋力推进“一优两高”，为建设更加富裕文明和谐美丽新青海作出新的更大贡献。",
          p2:
            "这次全国“两会”是在新中国成立70周年和全面建成小康社会、实现第一个百年奋斗目标的关键之年召开的一次十分重要的会议。习近平总书记在会议期间发表的重要讲话，着眼党和国家工作大局，提出一系列新观点、新论断、新要求，具有极强的思想性、战略性、指导性，为全党全国全军建功新时代、实现新作为提供了强大思想武器和重要行动指南。中央政治局常委、国务院总理李克强参加青海代表团审议的重要讲话，对青海工作给予充分肯定，对青海发展提出明确要求。中央政治局委员、中央组织部部长陈希在青海代表团的重要讲话，为我们进一步做好青海工作注入了强大动力。",
          p3:
            "会议的结束，就是落实的开始。昨天，省委召开常委会议对全省学习贯彻全国“两会”精神作出了安排部署，强调要在学深悟透、融会贯通上下功夫，进一步找准工作的着力点，把青海工作谋得更实、抓得更紧、干得更好，确保全国“两会”精神在青海落地生根。下面，我就贯彻落实全国“两会”精神和省委要求，讲几点意见。",
          p4:'第一，在学懂弄通做实上下功夫。',
          p5:'全省各级群团组织要把学习宣传贯彻全国“两会”精神作为当前首要政治任务，深入基层一线、街道社区、农村牧区广泛开展学习研讨、宣传宣讲活动，迅速兴起学习宣传贯彻热潮，切实把广大群团干部、各族群众的思想和行动统一到习近平总书记重要讲话精神上来，统一到“两会”关于今年工作的决策部署上来，统一到省委省政府安排要求上来，凝聚起全省上下同心同德促发展、齐心协力奔小康的强大力量。',
          p6:'第二，在推动高质量发展上下功夫。',
          p7:'深入贯彻落实新发展理念，把握稳中求进工作总基调，围绕深化供给侧结构性改革，紧扣全省经济发展大局，坚持以“盐湖资源综合利用”、“清洁能源发展”“特色农牧业发展”、“文化旅游产业发展”等重点产业为主攻方向，广泛开展 “工人先锋号”、“青年文明号”、“巾帼建功号” 创建活动，充分激发各族群众劳动热情，创造激情，在深入实施 “五四战略”，奋力推进“一优两高”中建功立业。',
          p8:'第三，在加强生态环境保护上下功夫。',
          p9:'要树牢绿水青山就是金山银山的理念，始终牢记 “三个最大”，引导各族群众像保护眼睛一样保护生态环境，像对待生命一样对待生态环境，广泛开展 “植树造林绿化家园”“清洁三江源·保护母亲河”、“绿色工作低碳生活”等形式多样、内容丰富的专项活动，扎实推进国家公园省建设，积极推进环保督察反馈问题整改，助力打好污染防治攻坚战，让植绿护绿爱绿成为各族群众的自觉行动，让青海蓝、河湖清、山川绿永驻高原大地。',
          p10:'第四，在助力脱贫攻坚上下功夫',
          p11:'要始终坚持精准方略，坚持分类施策，整合资源，鼓足干劲，持续深化群团各自联系服务对象的解困脱困工作，确保小康路上一个都不能少。要加大推进定点扶贫村的脱贫攻坚力度，加强扶贫产业经营管理，切实发挥政治效益、社会效益、经济效益，确保联点村贫困人口如期脱贫，响鼓重锤打好打赢脱贫攻坚战。',
          p12:'第五，在实施乡村振兴上下功夫。',
          p13:'要坚持以产业发展为根本，扎实做好高原美丽乡村建设，促进脱贫攻坚和乡村振兴融合发展。要充分发挥群团组织优势，广泛开展“科技、文化、卫生”三下乡活动，有效整合资源，加大农民工、青年、妇女技能培训力度，引导群众转变就业观念，让更多群众走出去创业就业。要加强核心价值观和公序良俗宣传教育，抵制陈规陋习，树立文明新风，全面推进乡村振兴。',
          p14:'第六，在保障和改善民生上下功夫。',
          p15:'要始终坚持以人民为中心的发展思想，精心实施好群团办实事项目，促进就业、教育、医疗、住房、社会保障等基本民生均等化、普惠化。要扎实开展送温暖、青年创业、关爱留守妇女儿童、助残活动、科学普及、博爱进万家等活动，千方百计为群众排忧解难，让各族群众体面工作、尊严生活、全面发展，进一步增强群众获得感、幸福感、安全感。',
          p16:'第七，在转变工作作风上下功夫',
          p17:'要精心谋划开展“不忘初心、牢记使命”主题教育，有效落实为基层减负措施，坚决整治形式主义、官僚主义，营造干事创业、风清气正的良好生态。扎实开展“大走访、大排查、大调研”活动，察实情、集民智，充分发挥群团界别人大代表政协委员作用，围绕群众关心的热点难点问题，提出高质量的议案和提案。要带头树立过紧日子的思想，艰苦奋斗、勤俭节约，发扬斗争精神、增强斗争本领，确保中央、省委各项决策部署在群团系统落地生根。',
          num: "2230",
          pic:'../../../static/img/42.png'
        }
      ]
    };
  }
};
</script>

<style scoped>
header {
  width: 100%;
  height: 3rem;
}
header .box {
  width: 85%;
  height: 3rem;
}
header i {
  font-size: 1.4rem;
  color: black;
}
/* 主题 */
.main {
  width: 100%;
}
.main .h1 {
  width: 90%;
  margin-left: 0.5rem;
}
.main h1 {
  font-size: 1.35rem;
  color: black;
  text-align: left;
  line-height: 1.8rem;
  letter-spacing: 0.15rem;
}
.main .p1 {
  width: 95%;
  margin: 0 auto;
}
.main .p1 p {
  text-align: left;
  font-size: 1.02rem;
  color: black;
  margin-top: 1rem;
  line-height: 1.45rem;
  letter-spacing: 0.12rem;
}
.main .p1 strong {
  font-weight: bold;
}
.main .img {
  width: 95%;
  height: 12rem;
  margin: 0 auto;
  margin-top: 1rem;
}
.main .img img {
  width: 100%;
  height: 12rem;
}
.main footer {
  width: 100%;
  height: 3rem;
}
.main footer p{
  font-size:0.9rem;
  color:#b0b0b0;
}
.main footer i{
   margin-left:2%; 
   color:#b0b0b0;
}
.main footer span{
   font-size:0.9rem;
   color:#b0b0b0;
}
</style>


